/* ----- NOMINATION APPROVAL STYLES ----- */
.page#nominationsApprovalPageView {
    ul {
        list-style: none;
        margin: 0;
    }

    a {
        cursor: pointer;
        //font-weight: $fontWeightBold;
    }

    .spincover {
        background: rgba(255, 255, 255, .5);
        bottom: 0;
        left: 0;
        position: absolute;
        right: 0;
        top: 0;
        z-index: 10000;
    }

    .spin {
        display: block;
        height: $spinner_widthHeight;
        top: 50%;
        left: 50%;
        position: absolute;
        width: $spinner_widthHeight;
    }

    .nominationTableControls {
        margin-top: 15px;
        margin-left: 30px;

        a + a {
            margin-left: 40px;
        }
    }

    .approvalPromotionWrapper {
        h3 {
            // margin-left: 30px;
            margin-top: 0;
            // line-height: 30px;
        }
    }

    .nominationPromotionName {
        font-size: $fontSizeLarge;
    }

    #nominationChangePromoBtn {
        margin-left: 30px;
        // vertical-align: text-bottom;
    }

    .doViewRules {
        margin-left: 30px;
    }

    #rulesModal .modal-body {
        max-height: 400px;
    }

    .export-tools .icon-print {
        font-size: 25px;
    }

    .approverLevelsWrap {
        margin-top: 30px;

        .approverLevel {
            float: left;
            min-height: 100px;
            padding-right: 10px;
            width: 45%;
            min-width: 110px;
            word-wrap: break-word;

            h5 {
                margin-top: 0;
            }

            & + .approverLevel {
                border-left: 1px solid #000;
                padding-left: 10px;
            }
        }

        .singleLevel{
            width: 100%;
        }
    }

    .approvalsExportIconsWrapper.pushDown {
        margin-top: 15px;
    }

    .approvalTableWrap {
        margin-left: 0;
        overflow: auto;
    }

    .approvalBudgetWrap {
        h4 {
            margin-top: 0;
        }

        .budgetTimestamp {
            color: $brand_color_dark;
            display: block;
            font-size: 12px;
        }
    }

    .approvalSearchWrapper {
        table .date {
            width: 110px;
        }

        #approvalsSearchForm {
            margin-bottom: 0;
        }
    }

    .approvalTopRightCont {
        border: 1px solid #ccc;
        box-sizing: border-box;
        float: right;
        padding: 15px;
    }

    .nominationErrorQTip {
        //gross important needed for qtip inside modal.
        z-index: 25000 !important;
    }

    //Details + carousel
    .detailCarousel {
        display: inline-block;
        margin-right: 40px;
        padding: 0 20px;
        position: relative;
        width: 46%;

        .item {
            ul {
                list-style: disc;
            }
        }

        .cycle {
            margin-left: 50px;

            .dl-horizontal {
                dt {
                    clear: both;
                    text-align: left;
                    white-space: normal;
                    word-wrap: break-word;
                }

                dt,
                dd {
                    display: block;
                    float: left;
                    padding-bottom: 20px;
                }

                dd {
                    margin-left: 20px;
                    width: 40%;
					&.translateTextLink{
						width: auto;
						.translateLinkDisabled{
							width: auto;
						}
					}
                }
            }

        }

        .navArrow {
          color: $grayDarker;
          cursor: pointer;
          font-size: 40px;
          position: absolute;
          top: 50%;

            &.next {
                right: 16%;
            }

            &.prev {
                left: 3.5%;
            }
        }

        &.active {
            top: 5px;
            vertical-align: top;
        }

        &.cumulativeCarousel {
            width: 57%;

            .item {
                width: 80%;
            }
        }
    }

    .eCardVideoWrap {
        display: inline-block;
        margin-top: 15px;
        padding: 0 20px;
        vertical-align: top;

        span {
            display: block;
            font-weight: bold;
            padding-bottom: 10px;
        }

        img {
            width: 104px;
        }
    }

    .behaviorWrap {
        margin-right: 50px;
        margin-top: 15px;
        width: 240px;

        .behaviorLabel {
            display: block;
            font-weight: bold;
            padding-bottom: 5px;
        }

        li {
            float: left;
            margin-right: 5px;

            span {
                display: block;
                text-align: center;

                &.noImg {
                    line-height: 40px;

                    &::after {
                        content: ",";
                    }
                }
            }

            img {
                display: block;
                margin: 0 auto;
                width: 72px;
            }

            &:last-child .noImg:after {
                content: " ";
            }
        }
    }

    .inline {
        display: inline-block;
        padding-right: 5px;
    }

    .currentBalance {
        display: block;
        padding-bottom: 15px;
        padding-top: 5px;
    }

    .large-text {
        font-size: $baseFontSize * 2;
        line-height: $baseFontSize * 2 + 2;
    }

    /* Comparison Blocks */
    .compareNomineeBlock {
        background-color: $nominationsApproval_compareBgColor;
        box-sizing: border-box;
        display: inline-block;
        float: left;
        height: 405px;
        padding: 5px 10px 5px 15px;
        width: 320px;

        .image-holder {
            display: block;
            height: 100px;
            margin: 0 auto 15px;
            width: 100px;
            .avatar {
               display: block;
                margin: 20px auto;
                max-width: 100%;
            }

            i {
                border: 0;
                font-size: 60px;
                height: auto;
                line-height: 18px;
                max-width: 100%;
                text-align: center;
                vertical-align: middle;
                width: auto;

                &:before {
                    $experimental-support-for-svg: true;
                    background: $g5AvatarBg;
                    @include background($g5AvatarBg linear-gradient(to bottom, $g5AvatarBg, lighten($g5AvatarBg, 20%)));

                    border-radius: 50%;
                    color: $white;
                    display: block;
                    height: 100px;
                    line-height: 100px;
                    margin: 20px auto;
                    overflow: hidden;
                    width: 100px;
                }
            }
        }

        .nomineeName {
            color: $linkColor;
            display: block;
            font-weight: bold;
        }

        .compareDetails {
            margin-top: 10px;
            margin-bottom: 15px;
            max-height: 220px;
            overflow-x: auto;

            span,
            strong {
                display: block;
                margin-top: 5px;
            }

            .nomineeMeta {
                display: inline-block;
                margin: 0;
            }

            .nomineeName {
                margin-bottom: 0;
            }

            span {
                margin-bottom: 10px;
            }
        }

        & + .compareNomineeBlock {
            margin-left: 5%;
        }
    }

    //Table Styling
    #nominationApprovalTable {
        select {
            margin-bottom: 0;
            width: 200px;
        }

        textarea {
            width: 186px;
        }

        td {
            vertical-align: middle;
        }

        .input-medium {
            width: 110px;
        }

        .awardAmount {
            display: block;
        }

        .flash {
            background: #ccc;
        }

        .expandRow {
            text-align: center;
            // vertical-align: middle;

            .expandCollapseRow,
            .cumulativeExpand,
            .cumulativeCollapse  {
                color: #bebebe;
                cursor: pointer;
                font-size: 20px;
            }
        }

        .validateForCalc {
            width: 0;
            height: 0;
            padding: 0;
            border: 0;
            margin-top: -100%;
            margin-left: 100%;
        }

        .date {
            text-align: left;
        }

        .compareRow {
            text-align: center;
            // vertical-align: middle;
        }

        .nominator a {
            display: block;
        }

        tbody tr td {
            background-color: transparent;
        }

        tbody tr.odd td {
            background-color: $tableBackgroundAccent;

            &.flash {
                background: transparentize($brand_color_primary, 0.5);
            }
        }

        .notificationDate {
            .datepickerTrigger {
                padding-right: 0;
            }
        }


        tr.detailRow {
            // background-color: $nominationsApproval_tableEvenBgColor;

            &.odd {
                // background-color: $white;
            }

            td {
                height: 100px;
                padding: 40px 8px;
            }
        }

        .dateSubmitted > * {
            display: block;
        }

        .attachMessageCont {
            margin-top: 10px;
        }
    }

    .approvalsTeamTooltip {

        .teamMembersTooltip {
            max-height: 400px;
            overflow-x: auto;

            ul{
                padding: 0 15px;

            }

        }
    }

    .mobileDetectTooltip {
        margin-top: 10px;
    }

    //Team Award Modal
    .teamHeader {
        border-bottom: 1px solid #000;
        padding-bottom: 10px;
        margin-bottom: 10px;

        span {
            display: inline-block;
            width: 48%;
        }
    }

    .teamRecipientList ul {
        margin-top: 10px;
        margin-left: 20px;

        li {
            .paxInfo,
            .awardInfo {
                display: inline-block;
                margin-bottom: 10px;
                vertical-align: top;
                width: 45%;
            }
        }
    }

    .approvalSuccessModal {
        text-align: center;
    }
}

/* ----- NOMINATION INPROGRESS LIST PAGE STYLES ----- */
.page.nominationsApprovalPromoPage {
    .tableTitle {
        font-size: $baseFontSize * 1.71429;
        font-weight: bold;
        margin-bottom: 22px;
    }

    .msgEmpty {
        margin: 10px;
    }

    .spincover {
        background: rgba(255, 255, 255, .5);
        bottom: 0;
        left: 0;
        position: absolute;
        right: 0;
        top: 0;
        z-index: 10000;
    }

    .spin {
        display: block;
        height: $spinner_widthHeight;
        top: 50%;
        left: 50%;
        position: absolute;
        width: $spinner_widthHeight;
    }
}

/* ----- NOMINATIONS MORE INFO STYLES ----- */
.page.nominationsMoreInfoPageView {
    .nomination-name {
		font-size: $baseFontSize * 1.42857;
		margin-bottom: 20px;
	}

	.attachmentHelpText {
        color: #666666;
        font-size: $baseFontSize * .86;
	}

   .nominationAddLink {
        clear: both;

        i {
            padding-right: 7px;
        }

        a {
            padding-right: 10px;
        }
    }

    .qtip {

        .teamMembersTooltip {
            max-height: 400px;
            overflow-x: auto;

            ul{
                margin: 0px 0 5px 15px;
                list-style: none;

            }

        }
    }

    .detailsData {
        clear: both;
    }

    .noImg::after {
        content: ",";
    }

    .badges:last-child .noImg:after {
        content: " ";
    }

	.nominationDisplayAttached {
        padding: 5px 0;
        position: relative;

        .spin {
            display: block;
            height: $spinner_widthHeight / 2;
            margin: 0;
            position: absolute;
            width: $spinner_widthHeight / 2;
        }
    }

	.removeLink {
        cursor: pointer;

        i {
            padding-right: 0;
            font-weight: $fontWeightBold;
        }
    }

	.attachUrl {
        vertical-align: top;
    }

    .addDocPopover {
        .control-group {
            margin-bottom: 0;

            label {
                cursor: default;
            }
        }

        form {
            line-height: $baseLineHeight * 1.1;
            margin-top: 10px;

            input {
                height: 25px;
                line-height: $baseLineHeight * .85;
                outline: none;
            }
        }
    }

	.nominationsMoreInfoForm {
        #nominationLink,
        #nominationLinkUrl {
			background-color: transparent;
			border: medium none;
			box-shadow: none;
			height: 0;
			outline: medium none;
			padding: 0;
			position: absolute;
			top: -15px;
			width: 170px;
			z-index: 0;
		}

		.moreInformation {
			box-sizing: border-box;
			min-height: 40px;
			width: 100%;
		}

        .control-label {
            padding: 0;
        }
	}

	.translateTextLink {
		//font-weight: $fontWeightBold;
	}

	.mediaContainer {
		margin: 40px auto;
		width: 250px;
	}

	.submissionTitle {
		border-bottom: 1px solid $black;
		font-size: $baseFontSize * 1.42857;
		margin-top: 20px;
		padding: 20px 0;
	}
}

@media (max-width: 1200px) {
    .page#nominationsApprovalPageView {
        .inline {
            display: block;
        }
    }

}
